@media (max-width: 480px) {
  .container {
    padding: 0 15px;
  }

  .hover {
    z-index: 4;
    background: #fff;
    margin-top: -20px;
    padding: 15px;
    position: relative;
  }

  .header__burger-btn {
    width: 60px;
    height: 60px;
    z-index: 3;
    background-color: rgba(0, 0, 0, 0);
    border: 2px solid #b1cfe1;
    border-radius: 50%;
    margin-top: 15px;
    margin-bottom: 40px;
    margin-left: auto;
    display: flex;
    position: relative;
    box-shadow: 0 0 2px 2px rgba(154, 233, 242, .99);
  }

  .burger__svg {
    width: 80%;
    fill: #f7a1a8;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .nav__block .header__burger-btn .close {
    opacity: 0;
  }

  .nav__block .header__burger-btn .br {
    opacity: 1;
  }

  .nav__block.open .header__burger-btn .br {
    opacity: 0;
  }

  .nav__block.open .header__burger-btn .close {
    opacity: 1;
  }

  .nav__block {
    z-index: 2;
    display: block;
    position: relative;
  }

  .nav__list {
    z-index: 1;
    width: 100%;
    z-index: 2;
    background-color: #b1cfe1;
    flex-direction: column;
    gap: 0;
    transition: transform .7s;
    display: flex;
    top: 0;
    right: 0;
    transform: translateY(-280%);
  }

  .nav__list.-top {
    margin-top: -2px;
    padding-bottom: 25px;
  }

  .nav__block.open .nav__list {
    transform: translateY(0);
  }

  .nav__item {
    padding-top: 25px;
  }

  .nav__link {
    color: #f7a1a8;
    text-shadow: 5px 3px 5px #1b1a1a;
    padding: 15px 80px 15px 30px;
    font-size: 34px;
    font-weight: 700;
    line-height: 2;
  }

  .nav__logo {
    max-width: 30%;
    z-index: 5;
    position: absolute;
    top: 40px;
    left: 15px;
  }
}

